import { useState, useEffect } from 'react';
import httpFetch from '../fetch';

export default function Home() {
  const [services, setServices] = useState([]);
  useEffect(() => {
    async function fetchData() {
      try {
        const result = await httpFetch('http://www.engadget.com/rss.xml');
        setServices([result]);
      } catch (err) {
        console.log('err', err);
      }
      
    }
    fetchData();
  }, [/*services*/]);
  return <div>Welcome to Next.js! {services.length ? '' + services[0] : null}</div>
}